import React from "react";
import {  Switch, Route } from "react-router-dom";

import TodaysWeather from "../TodaysWeather/TodaysWeather";
import Prognosis from "../Prognosis/Prognosis";
import Current from "../Current/Current";

class Main extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            unitType: "Metric",
            weather: this.props.weatherSI
        }
    }

    convertUnits = () => {
        (this.state.unitType === "Metric") 
        ? this.setState({
            ...this.state, weather: this.props.weatherUS, 
            unitType: "American"
        })
        : this.setState({
            ...this.state, weather: this.props.weatherSI, 
            unitType: "Metric"
        })
    }
    
    render() {
        const { weather, unitType } = this.state;
            return (
                <main>
                    <button className="btn btn-small" onClick={this.convertUnits}>
                        {(this.state.unitType==="Metric") ? "Display American" : "Display Metric"}
                    </button>
                    <Switch >
                        <Route exact path="/current" render={() => <Current unitType={unitType} weather={weather.currently}/>}/>
                        <Route path="/week" render={() => <Prognosis unitType={unitType} weather={weather.daily}/>}/>
                        <Route path="/today" render={() => <TodaysWeather unitType={unitType} weather={weather.hourly}/>}/>
                    </Switch>
            </main>
        )
    }
}

export default Main;